body {
  color: #666;
  font-family: "Orbitron", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

:root {
  --main: #1cbdb4;
  --sub: #a8e9e5;
  --dark: #333;
  --blue: #00aae5;
  --green: #f68b1e;
  --orange: #8cc63f;
}

html {
  margin-top: 0 !important;
}

#wpadminbar {
  display: none !important;
}

body {
  height: 100vh;
  background-size: 250px, 100%;
  &.gray {
    background-image: url(../assets/img/pattern1.png), linear-gradient(#fff, #0e0d0d);
    #root {
      background: linear-gradient(#fff, #0e0d0d);
      color: #333;
      article.dday-list ul {
        @media (hover: hover) {
          &::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            background-color: #f5f5f5;
          }
          &::-webkit-scrollbar {
            width: 6px;
            background-color: #f5f5f5;
          }
          &::-webkit-scrollbar-thumb {
            background-color: #585858;
          }
        } //hover
      } // ul
    }
  } // body.gray

  /* 
수박바 = #ebbcd5, #2e7c51, #000000
*/

  /* 
자두 = #c1addd, #ec1818, #13380e
*/

  &.pink {
    background-image: url(../assets/img/pattern2.png), linear-gradient(#c1addd, #ec1818);
    #root {
      background: linear-gradient(#c1addd, #fa4b4b);
      // color: rgba(#FFF, 0.8);
      color: rgba(#13380e, 0.8);
      article.dday-list ul {
        @media (hover: hover) {
          &::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            background-color: #f5f5f5;
          }
          &::-webkit-scrollbar {
            width: 6px;
            background-color: #f5f5f5;
          }
          &::-webkit-scrollbar-thumb {
            background-color: #d34a78;
          }
        } //hover
      } // ul
    }
  } // body.pink

  &.skyblue {
    background-image: url(../assets/img/pattern3.png), linear-gradient(#fafafa, #398d9c);
    #root {
      background: linear-gradient(#fafafa, #398d9c);
      color: #266df1;
      article.dday-list ul {
        @media (hover: hover) {
          &::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            background-color: #f5f5f5;
          }
          &::-webkit-scrollbar {
            width: 6px;
            background-color: #f5f5f5;
          }
          &::-webkit-scrollbar-thumb {
            background-color: #327cb8;
          }
        } //hover
      } // ul
    }
  } //body.skyblue

  #root {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: min(95%, 400px);
    height: min(95%, 600px);
    /* background: #666; */
    overflow: hidden;
    overflow-y: auto;
    box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
    section {
      min-height: 100%;
      box-sizing: border-box;
      padding: 15px;
      h1,
      h2 {
        text-align: center;
        margin-bottom: 20px;
        font-weight: 700;
        font-size: 1.7em;
        text-shadow: -2px -2px 1px rgba(255, 255, 255, 1), 3px 3px 1px rgba(0, 0, 0, 0.1);
      }
    } //section
    .back-btn {
      z-index: 10;
      position: absolute;
      right: 0;
      top: 0;
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      font-size: 1.3em;
      transform: scale(-1);
      &:hover {
        color: #ccc;
        text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
      }
    }
  } //root
} //body

@keyframes cursor {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
